import React, { useContext } from "react";
import styled from "styled-components";
import { theme } from "../context/themeContext";
import Me from "../Image/aboutMe.png";
import { Mobile } from "../responsive";

const Container = styled.div`
  height: 100vh;
  max-width: 100%;
  display: flex;
  padding: 0 100px 0px 150px;
  ${Mobile({ padding: "0px", height: "auto" })}
`;

const Left = styled.div`
  height: 100%;
  flex: 1;
  display: flex;
  align-items: center;
  ${Mobile({ display: "none" })}
`;

const Image = styled.div`
  width: 50%;
  height: 70vh;
  border-radius: 30px;
  border: 3px solid #333;
  box-shadow: -50px -50px 0px 0px #333;
  padding: 10px;

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
`;

const Right = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 5%;
  padding: 45px 0px;
  align-items: center;
  display: flex;
  /* border: 2px solid red; */
  ${Mobile({ padding: "60x 0px", width: "100%" })}

  & h1 {
    text-decoration: underline;
    ${Mobile({ paddingBottom: " 25px" })}
  }
`;

const Description = styled.div`
  /* background-color: green; */
  ${Mobile({ width: "100%" })}

  & h3 {
    /* background-color: red; */
    ${Mobile({ textAlign: "center", padding: "0px 25px" })}
  }
`;

const PA = styled.p`
  padding-bottom: 15px;
  font-size: 17px;
  font-weight: 600;
  /* background-color: red; */
  ${Mobile({ padding: "0px 25px", textAlign: "center" })}
`;

const PB = styled.p`
  padding-bottom: 25px;
  text-align: justify;
  font-size: 16px;
  /* background-color: red; */
  ${Mobile({ padding: "25px", fontSize: "17px" })}
`;

const Technologies = styled.div`
  padding-top: 15px;
  display: flex;
  width: 100%;
  justify-content: space-between;
  /* border: 2px solid red; */

  ${Mobile({ flexWrap: "wrap", justifyContent: "flex-start" })}

  & .techStack {
    height: 70px;
    width: 70px;
    object-fit: cover;
  }
`;

const Tech = styled.div`
  ${Mobile({ margin: "4%" })}
  padding: 8px 0px;
  border-radius: 10px;
  transition: 0.5s border ease-in-out;
  border: 1px solid
    ${({ themevalue }) => (themevalue ? "lightgray" : "#373737")};
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 100px;
  width: 100px;
  ${Mobile({ height: "33%", width: "25%" })}

  & p {
    font-size: 12px;
    font-weight: 600;
  }
`;

